@import "./scss/index.scss";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $background-grey-dark;
  color: $font-light;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn {
  box-shadow: none !important;
}

.btn.btn-primary {
  background-color: $primary;
  border-color: $primary;
  &:hover {
    background-color: rgba(26, 145, 218, 1);
  }
}

.btn.btn-outline-primary {
  border-color: $border-primary;
  &:hover,
  &:active,
  &:focus {
    background-color: rgba(29, 261, 241, 0.1) !important;
  }
}

input.form-control,
textarea.form-control {
  background-color: $background-dark-light;
  border-radius: 0;
  border: 0;
  border-bottom: 1px solid $border-grey;
  color: $font-light !important;

  &:focus {
    background-color: $background-dark-light;
    box-shadow: none;
    border-bottom: 1px solid $border-grey;
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:active,
input:-webkit-autofill:focus {
  background-color: $background-dark-light !important;
  color: $font-light !important;
  -webkit-box-shadow: none !important;
  -webkit-text-fill-color: #555555 !important;
}
